let DrawFn = function(option,fn) {
    let self = this;
	self.configObj = option;
	self.callback = fn;
    self.init();
};
DrawFn.prototype.init = function(params) {
    let self = this;
    // 新建canvas 元素
    // let targetBox = document.querySelector(self.configObj.el);
    let newCanvas = document.createElement("canvas");
    newCanvas.id = "drawCanvas";
    newCanvas.width = self.configObj.designW;
    newCanvas.height = self.configObj.designH;
    // targetBox.appendChild(newCanvas);
    // 新建canvas 对象
    let ctx = newCanvas.getContext("2d");
    let img = new Image();
	let drawlist = self.configObj.itemArray;
	let preloadList =[]
	//排列层级
    drawlist.sort(function(n, m) {
        n.zIndex = n.zIndex || 0;
        m.zIndex = m.zIndex || 0;
        return n.zIndex - m.zIndex;
	});
	drawlist.forEach((item)=>{
		if(item.type == 'img'){
			preloadList.push(item.content)
		}
	})
	preloadFn(preloadList,replay_draw,self.configObj.error)
    //背景颜色
    ctx.fillStyle = self.configObj.bgColor;
    ctx.fillRect(0, 0, self.configObj.designW, self.configObj.designH);
    //开始画元素
    // replay_draw();
    function replay_draw() {
         /*
        循环执行绘制元素
        resolve 继续执行绘制
        reject 生成图片
        */
        buildElementInCanvas(drawlist.shift()).then(
            function() {
                replay_draw();
            },
            function() {
                buildImg();
            }
        );
    }
    function buildElementInCanvas(obj) {
        /*
        绘制元素
        arguments:
            obj: 传入元素对象（itemArray）
        return： 
            返回promise对象
        */
        let promise = new Promise(function(resolve, reject) {
            switch (obj.type) {
                case "img":
                    img.src = obj.content;
                        // 是否圆形图片
                        obj.isCricle
                            ? circleImg(ctx, img, obj.x, obj.y, obj.w / 2)
                            : ctx.drawImage(img, obj.x, obj.y, obj.w, obj.h);
                        drawlist.length == 0 ? reject() : resolve();
                    break;
                case "text":
                    for (let key in obj.style) {
                        ctx[key] = obj.style[key];
                    }
                    wrapFN(obj.nowrap == "auto" ? 1 : 0, obj);
                    drawlist.length == 0 ? reject() : resolve();
                    break;
                default:
                    break;
            }
        });
        return promise;
    }
    function wrapFN(type, obj) {
         /*
        绘制换行文本
        arguments:
           type:1 自动换行：通过宽度换行 ； 2：一行放固定数字文本 
        @params: 
            arrText:文案数组
            x：元素x轴位置
            y：元素y轴位置
        */
        let arrText = obj.content.split("");
        let line = "";
        let x = obj.x;
        let fontSize = parseInt(ctx.font.match(/\d+px/)[0]);
        let y = obj.y + fontSize;
        switch (type) {
            case 1: //自动换行
                for (let n = 0; n < arrText.length; n++) {
                    let testLine = line + arrText[n];
                    let metrics = ctx.measureText(testLine);
                    let testWidth = metrics.width;
                    if (testWidth > obj.w && n > 0) {
                        ctx.fillText(line, x, y);
                        line = arrText[n];
                        y += obj.lineHeight;
                    } else {
                        line = testLine;
                    }
                }
                ctx.fillText(line, x, y);
                break;
            case 0: //字数换行
                let lineNumber = 1;
                for (let n = 0; n < arrText.length; n++) {
                    let testLine = line + arrText[n];
                    if (obj.nowrap < lineNumber && n > 0) {
                        lineNumber = 1;
                        ctx.fillText(line, x, y);
                        line = "";
                        y += obj.lineHeight;
                    } else {
                        lineNumber++;
                        line = testLine;
                    }
                }
                ctx.fillText(line, x, y);
                break;
        }
    } //文本换行
    function circleImg(ctx, img, x, y, r) {
        /*
        绘制圆形图片；
        arguments:
            ctx:canvas对象
            img：图片对象
            x：圆型图片左上角坐标x
            y：圆型图片左上角坐标y
            r：圆半径
        @params:
        cx,cy:圆心
        */
        ctx.save();
        var d = 2 * r;
        var cx = x + r;
        var cy = y + r;
        ctx.arc(cx, cy, r, 0, 2 * Math.PI);
        ctx.clip();
        ctx.drawImage(img, x, y, d, d);
        ctx.restore();
    } //圆形图片
    function buildImg() {
        /*
        生成base64，在页面上显示出来
        */
        var img = new Image();
        let imageData = newCanvas.toDataURL("image/png");
        img.src = imageData;
        self.url = imageData;
		img.style.position = "absolute";
		img.style.left = 0;
		img.style.top = 0;
		img.style.width ="100%";
		img.style.height = "100%";
		img.style.objectFit = "contain";
		img.style.background = "#000"
        img.onload = function() {
			document.querySelector('#app').appendChild(img);
			self.configObj.success()
        };  
	}
	
		
    function preloadFn (array,onloadFn,onerrorFn) {
        let onreadyList = []
        array.forEach(function (item) {
            let img = new Image()
            img.src = item;
            
            img.onload = function (params) {
                onreadyList.push(item)
                if(onreadyList.length == array.length){
                    onloadFn()
                }
            }
            img.onerror = function (params) {
                onerrorFn(params,item)
                console.log('params')
            }
        })
    }
	
};
DrawFn.prototype.getImgBase = function(params) {
    /*
    返回图片base64
    */
    let self = this;
    return self.url;
};

export default DrawFn;


